import React from "react";


class Clock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            hours: null,
            minutes: null,
            seconds: null,
        };
        this.handleDate = this.handleDate.bind(this);
    }

    componentDidMount() {
        this.clockInterval = setInterval(this.handleDate, 1000);
    }

    componentWillUnmount() {
        clearInterval(this.clockInterval);
    }

    handleDate() {
        let date = new Date();
        let offset = (this.props.offset) ? this.props.offset : 0;
        offset = offset / 60;
        if (offset === 0) {
            this.setState({
                hours: date.getUTCHours(),
                minutes: date.getUTCMinutes(),
                seconds: date.getUTCSeconds(),
            });
            return;
        }
        let currentOffset = date.getTimezoneOffset() * 60000;
        let UTCTime = date.getTime() + currentOffset;
        let remoteTime = new Date(UTCTime + (3600 * 1000 * (offset)));

        let hours = remoteTime.getHours();
        let minutes = remoteTime.getMinutes();
        let seconds = remoteTime.getSeconds();

        this.setState({
            hours: hours,
            minutes: minutes,
            seconds: seconds,
        });
    }

    render() {
        let totalSeconds = this.state.seconds * 6;
        let minuteTransform = this.state.minutes * 6;
        let minuteCompensation = this.state.seconds / 60 * 6;
        let totalMinutes = minuteTransform + minuteCompensation;

        let hourTransform = this.state.hours * 30;
        let hourCompensation = this.state.minutes / 60 * 30;
        let totalHours = hourTransform + hourCompensation + (30 / 3600 * this.state.seconds) ;
        let hourStyle = {
            transform: `rotate(${totalHours}deg)`,
        };

        let minuteStyle = {
            transform: `rotate(${totalMinutes}deg)`
        };

        let secondStyle = {
            transform: `rotate(${totalSeconds}deg)`
        };
        
        let zone = (this.props.zone) ? this.props.zone : "UTC";

        return (
            <div className={"clock"}>
                <h3>Time</h3>
                <div className={"analog-clock"}>
                    <div className={"numbers"}>
                        <div className={"hour-12"}>12</div>
                        <div className={"hour-6"}>6</div>
                        <div className={"hour-3"}>3</div>
                        <div className={"hour-9"}>9</div>
                    </div>

                    <div className={"dial"}>

                        <div className={"seconds"} style={secondStyle}>
                            <svg className={"seconds-svg"}>
                                <rect className={"seconds-tick"} />
                            </svg>
                        </div>

                        <div className={"minutes"} style={minuteStyle}>
                            <svg className={"minutes-svg"}>
                                <rect className={"minutes-tick"} />
                            </svg>
                        </div>

                        <div className={"hours"} style={hourStyle}>
                            <svg className={"hours-svg"}>
                                <rect className={"hours-tick"} />
                            </svg>
                        </div>

                    </div>
                    <div className={"pin-cover"}>
                        <div id={"pin"}></div>
                    </div>
                </div>
                <div className={"zone-info"}>
                    {zone}
                </div>
                <div className={"digital-clock"}>
                    {(this.state.hours > 10) ? this.state.hours : `0${this.state.hours}`}:
                    {(this.state.minutes > 10) ? this.state.minutes : `0${this.state.minutes}`}:
                    {(this.state.seconds > 10) ? this.state.seconds : `0${this.state.seconds}`}
                </div>
            </div>
        );
    }
}


export default Clock;