import React from "react";
import ReactDOM from "react-dom";
import './assets/style.scss';
import Clock from "./components/clock";
import Country from "./components/countries"

const ctzone = require('countries-and-timezones');

class ClockContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedZone: null,
            selectedOffset: null,
        };
        this.setTimezone = this.setTimezone.bind(this);
    }

    setTimezone(zone) {
        this.setState({
            selectedZone: zone,
            selectedOffset: ctzone.getTimezone(zone).dstOffset,
        });
    }

    render() {
        return(
            <div className={"int-clock-container"}>
                <Country onChange={this.setTimezone}></Country>
                <Clock zone={this.state.selectedZone} offset={this.state.selectedOffset}></Clock>
            </div>
        );
    }

}

//
ReactDOM.render(
    <div style={{display: "flex", margin: "30px"}}>
        <ClockContainer></ClockContainer>
    </div>,
    document.getElementById("root"),
);