import React from "react";
const ctzone = require('countries-and-timezones');


class Countries extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTimezone: null,
            selectedOffset: null,
        };
    }

    render() {
        let countryList = ctzone.getAllCountries();
        return (<div className="country-list">
            <label htmlFor={"country-list"} className={"country-list-label"}>
                Select Timezone: 
            </label>

            <select name={"country-list"} id={"country-list"} onChange={pickEvent => this.props.onChange(pickEvent.target.value)}>
                <option key={"UTC"}>{"UTC"}</option>
                {Object.entries(countryList).map(
                    (item) => 
                    <optgroup key={item[0]} label={item[1].name}>
                        {item[1].timezones.map((value) => <option key={value}>{value}</option>)}
                    </optgroup>
                )}
            </select>
        </div>);
    }
}

export default Countries;